import React, { Component } from 'react';
import Development from '../components/Development';
import Design from '../components/Design';
import Print from '../components/Print';
import About from '../components/AboutMe';
import {
  Route,
  NavLink,
  HashRouter
} from "react-router-dom";

class Nav extends Component {
  constructor(props) {
    super(props);
    this.state = {addClass: false}
  }
  toggle() {
    this.setState({addClass: !this.state.addClass});
  }
  render() {
    let boxClass = ["box"];
    let logoClass = ["small"];
    if(this.state.addClass) {
      boxClass.push('green');
      logoClass.push('small');
    }
    return(
        <HashRouter>
         <nav>

  <input id="nav" type="checkbox" />

  <label for="nav">
    <strong>Menu</strong>
    <b><i></i><i></i><i></i></b>
  </label>

  <menu>
      <li><NavLink to="/development"><ruby>Development <rp>(</rp><rt>ディベロップメント</rt><rp>)</rp></ruby></NavLink></li>
      <li><NavLink to="/design"><ruby>Design <rp>(</rp><rt>デザイン</rt><rp>)</rp></ruby></NavLink></li>
      <li><NavLink to="/about"><ruby>About <rp>(</rp><rt>アバウト</rt><rp>)</rp></ruby></NavLink></li>
  </menu>

</nav>
           <Route path="/development" component={Development}/>
            <Route path="/design" component={Design}/>
          <Route path="/print" component={Print}/>
            <Route path="/about" component={About}/>
      </HashRouter>
    );
  }
}

export default Nav;